/* Font Chữ */
@font-face {
  font-family: "LilitaOneCustom";
  src: url("./assets/fonts/Lilita_One/LilitaOne-Regular.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "PoppinsCustom";
  src: url("./assets/fonts/Poppins/Poppins-Black.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "PoppinsExtraBold";
  src: url("./assets/fonts/Poppins/Poppins-ExtraBold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
body {
  font-family: "Poppins", Arial, sans-serif;
  background: #fff;
}
/* CSS của phần Top Header -------------------------------------------------------------------------------------------------*/
.topbar {
  width: 99vw;
  background: #323232;
  color: #fff;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: Arial, sans-serif;
  font-size: 16px;
  position: relative;
  z-index: 100;
}

.topbar-left {
  display: flex;
  align-items: center;
  height: 100%;
  padding-left: 20px;
}

.colorlib-logo {
  font-weight: bold;
  letter-spacing: -1px;
  font-size: 20px;
  color: #fff;
}

.colorlib-dot {
  color: #80e36b;
  font-size: 22px;
}

.topbar-title {
  margin-left: 21px;
  color: #bdbdbd;
  font-size: 16px;
  letter-spacing: 1px;
  background-color: #222222;
  padding: 20px 35px 20px 20px;
  font-family: "Open Sans", sans-serif;
}

.topbar-right {
  display: flex;
  align-items: center;
  height: 100%;
  padding-right: 3px;
}

.topbar-right:hover {
  background-color: #333333;
}

.topbar-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 100%;
  font-size: 24px;
  color: #bdbdbd;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
}

/* Hover cho tất cả các icon */
.topbar-icon:hover {
  background-color: #222222;
  color: #ffffff;
}

/* Hover cho cart khi active */
.topbar-icon.cart.active:hover {
  background-color: #222222;
  color: #fff;
}

.topbar-icon.cart.active {
  width: 60px;
  font-size: 18px;
  background: #7ac64d;
  color: #fff;
  border-radius: 0;
}

.topbar-icon.cart.active:hover {
  color: #fff;
}

/* CSS của phần Header -------------------------------------------------------------------------------------------------*/
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 85.2px;
  background: transparent;
  position: relative;
  z-index: 2;
  background: linear-gradient(to right, #ebefff 50%, #fff 50%);
}

.logo {
  position: relative; /* Cho phép phần tử con dùng absolute dựa trên nó */
  display: flex;
  align-items: center;
  font-weight: 1000;
  font-size: 26px;
  margin-left: 12%;
}

.logo-circle {
  position: absolute;
  left: 0;
  z-index: 1;
  background: #3b5bfe;
  color: #fff;
  border-radius: 50%;
  width: 37px;
  height: 37px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 26px;
  margin-left: 15px;
  font-weight: 900;
  font-family: "PoppinsExtraBold", Arial, sans-serif;
}

.logo-text {
  position: relative;
  padding-left: 24px; /* chừa chỗ cho logo-circle */
  color: #22223b;
  z-index: 2;
  font-weight: 900;
  font-family: "PoppinsExtraBold", Arial, sans-serif;
}
.highlightR {
  color: white;
  font-weight: 900;
  font-family: "PoppinsExtraBold", Arial, sans-serif;
}

.header nav {
  display: flex;
  gap: 36px;
  margin-right: 15vw;
}

.header nav a {
  color: #22223b;
  text-decoration: none;
  font-size: 16px;
  transition: color 0.2s;
  font-family: "Poppins", Arial, sans-serif;
  padding: 0 0 2px;
  position: relative;
  display: inline-block;
  transition: color 0.3s ease;
  letter-spacing: 1px;
  font-weight: 400;
}
.header nav a::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) scaleX(0);
  transform-origin: center;
  width: 100%;
  height: 2px;
  background-color: #3b5bfe;
  transition: transform 0.3s ease;
}
.header nav a:hover::after {
  transform: translateX(-50%) scaleX(1);
}
.nav-link {
  display: block;
}

/* Phần giới thiệu nổi bật hero section---------------------------------------------------------------------------------*/
.hero {
  width: 99vw;
  min-height: 660px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin: auto;
  background: linear-gradient(to right, #ebefff 50%, #fff 50%);
}

.hero-content {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
  position: relative;
  z-index: 1;
  margin-bottom: 140px;
}

.hero-subtitle {
  color: #3b5bfe;
  font-weight: 700;
  letter-spacing: 4px;
  font-size: 16px;
  margin-bottom: 18px;
  margin-top: 40px;
  font-family: "PoppinsCustom", sans-serif;
}

.hero-title {
  font-size: 60px;
  font-weight: 900;
  margin: 0 0 18px 0;
  color: #111;
  line-height: 1.1;
  font-family: "PoppinsExtraBold", Arial, sans-serif;
}

.hero-desc {
  font-size: 32px;
  font-weight: 700;
  color: #22223b;
  margin-bottom: 32px;
  font-family: "Poppins", Arial, sans-serif;
}

.hero-highlight {
  color: #3b5bfe;
  text-decoration: underline;
  font-weight: ̣900;
  font-size: 32px;
  font-family: "PoppinsExtraBold", Arial, sans-serif;
}

.hero-arrow-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
  position: relative;
  height: 70px;
}

.hero-arrow-wrapper::before {
  content: "";
  position: absolute;
  width: 68px;
  height: 68px;
  background: #ebefff;
  transform: rotate(45deg);
  z-index: 0;
}

.hero-arrow {
  position: relative;
  font-size: 30px;
  color: #3b5bfe;
  z-index: 1;
  animation: arrowDown 2.3s infinite;
  user-select: none;
  line-height: 1;
}


@keyframes arrowDown {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(12px);
  }
  100% {
    transform: translateY(0);
  }
} /*Hiệu ứng này em tham khảo ở https://tenten.vn/tin-tuc/transform-css-la-gi/*/

/* About Section */
.about {
  width: 100%;
  background: #fff;
  padding: 0;
}

.about-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
}

.about-row {
  display: flex;
  align-items: stretch; /* Cái này đảm bảo 2 bên bằng chiều cao */
  background: #f8f9fa;
  box-shadow: 0 10px 40px rgba(60, 72, 88, 0.07);
  overflow: hidden;
}
.about-left {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 500px;
  margin-left: 2.9vw;
}

.about-right {
  flex: 1.2;
  padding: 0px 60px 0px 60px;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.about-img {
  width: 100%;
  max-width: 555px;
  height: auto;
  object-fit: cover;
  display: block;
  margin: 0 auto;
  aspect-ratio: 555 / 683;
}

.about-title {
  font-size: 42px;
  font-weight: 800;
  margin-bottom: 18px;
  color: #111;
  letter-spacing: -1px;
}

.about-desc {
  color: #8b8b99;
  font-size: 18px;
  margin-bottom: 32px;
  line-height: 1.7;
}

.about-info {
  list-style: none;
  padding: 0;
  margin: 0 0 30px 0;
}

.about-info li {
  margin-bottom: 12px;
  font-size: 17px;
  color: #222;
  display: flex;
  line-height: 1.5;
}

.about-info span {
  font-weight: 700;
  min-width: 140px;
  color: #111;
  margin-right: 10px;
  display: inline-block;
}

.about-projects {
  font-size: 20px;
  margin-bottom: 32px;
  color: #222;
}

.about-number {
  color: #4ea8de;
  font-weight: 700;
  font-size: 22px;
  margin-right: 4px;
}

.about-btn {
  display: inline-block;
  background: #4ea8de;
  color: #fff;
  padding: 14px 36px;
  border-radius: 28px;
  font-weight: 700;
  font-size: 16px;
  text-decoration: none;
  letter-spacing: 1px;
  transition: background 0.2s;
  border: none;
  margin-top: 10px;
  box-shadow: 0 4px 18px rgba(78, 168, 222, 0.08);
}

.about-btn:hover {
  background: #22223b;
}


/*Phần hình ảnh*/
.partner-section {
  width: 100%;
  padding: 0;
}

.partner-container {
  max-width: 1520px;
  margin: 0 auto;
  background: #FFFFFF;
  padding: 32px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 64px;
}

.partner-logo {
  height: 65px;
  object-fit: contain;
}
/* Responsive */
/*màn to ở nhà*/
@media (min-width: 1800px) {
  .about-container {
    max-width: 1700px; /* Rộng hơn nữa */
    padding: 0 80px;
  }
  .about-left {
    margin-left: 5.2vw; /* Dịch phải nhiều hơn */
    min-height: 700px;
  }
  .about-img {
    max-width: 750px;
  }
  .about-right {
    padding: 0px 120px 80px 120px;
  }
  .about-title {
    font-size: 56px;
  }
  .about-desc {
    font-size: 24px;
  }
  .about-info li {
    font-size: 22px;
  }
  .about-projects {
    font-size: 28px;
  }
  .about-number {
    font-size: 32px;
  }
}

/*Phần khoảng trắng*/
.resume-section {
  display: flex;
  width: 100%;
  background: #fff;
  padding: 80px 0 60px 0;
  min-height: 700px;
  box-sizing: border-box;
}

.col-aside {
  width: 260px;
  background: #f8f9fa;
  border-radius: 24px;
  box-shadow: 0 10px 40px rgba(60,72,88,0.07);
  margin-right: 36px;
  padding: 48px 0 48px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: fit-content;
  position: sticky;
  top: 120px; /* Để cố định khi scroll */
}

.col-aside nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}

.col-aside nav ul li {
  margin-bottom: 14px;
  width: 100%;
}

.col-aside nav ul li:last-child {
  margin-bottom: 0;
}

.col-aside nav ul li a {
  display: block;
  padding: 18px 32px;
  color: #22223b;
  font-weight: 700;
  font-size: 18px;
  text-decoration: none;
  border-radius: 28px;
  background: transparent;
  transition: background 0.2s, color 0.2s;
  text-align: left;
}

.col-aside nav ul li.active a,
.col-aside nav ul li a:hover {
  background: #4ea8de;
  color: #fff;
}

.col-center {
  flex: 1;
  background: #fff;
  border-radius: 24px;
  box-shadow: 0 10px 40px rgba(60,72,88,0.07);
  padding: 48px 48px 48px 48px;
  min-width: 0;
}

.resume-title {
  font-size: 38px;
  font-weight: 800;
  margin-bottom: 38px;
  color: #111;
  letter-spacing: -1px;
}

.resume-class {
  display: flex;
  align-items: flex-start;
  margin-bottom: 36px;
}

.resume-class .icon {
  width: 60px;
  height: 60px;
  background: #4ea8de;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 26px;
  position: relative;
}

.resume-class .icon span {
  display: block;
  width: 28px;
  height: 28px;
  background: #fff;
  border-radius: 50%;
}

.resume-class .text {
  flex: 1;
  color: #22223b;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.7;
}

/* Responsive */
@media (max-width: 1199px) {
  .resume-section {
    flex-direction: column;
    padding: 40px 0 30px 0;
  }
  .col-aside {
    width: 100%;
    margin-right: 0;
    margin-bottom: 32px;
    position: static;
    border-radius: 16px;
    padding: 28px 0;
    flex-direction: row;
    justify-content: center;
  }
  .col-aside nav ul {
    display: flex;
    justify-content: center;
    gap: 8px;
  }
  .col-aside nav ul li {
    margin-bottom: 0;
  }
  .col-aside nav ul li a {
    padding: 12px 22px;
    font-size: 16px;
  }
  .col-center {
    padding: 28px 12px;
    border-radius: 16px;
    box-shadow: 0 4px 18px rgba(60,72,88,0.07);
  }
  .resume-title {
    font-size: 28px;
    margin-bottom: 22px;
  }
  .resume-class .icon {
    width: 44px;
    height: 44px;
    margin-right: 16px;
  }
  .resume-class .icon span {
    width: 18px;
    height: 18px;
  }
  .resume-class .text {
    font-size: 15px;
  }
}

@media (max-width: 600px) {
  .resume-section {
    padding: 12px 0;
  }
  .col-center {
    padding: 12px 4px;
  }
  .resume-title {
    font-size: 18px;
  }
}

/* Phần Research Responsive*/
@media (max-width: 768px) {
  .my-component {
    transform: scale(0.5);
    transform-origin: top left; /* giúp giữ vị trí ở góc */
  }
}
